.container {
    padding: var(--spacing-6);
    max-width: 1400px;
}

/* Stats Row */
.statsRow {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: var(--spacing-4);
    margin-bottom: var(--spacing-6);
}

@media (max-width: 768px) {
    .statsRow {
        grid-template-columns: repeat(2, 1fr);
    }
}

.miniStat {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-1);
    text-align: center;
}

.miniStatValue {
    font-size: var(--font-size-2xl);
    font-weight: 700;
    color: var(--color-accent);
}

.miniStatLabel {
    font-size: var(--font-size-sm);
    color: var(--color-gray-400);
}

/* Toolbar */
.toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: var(--spacing-4);
    margin-bottom: var(--spacing-5);
}

.searchWrapper {
    width: 320px;
}

@media (max-width: 640px) {
    .toolbar {
        flex-direction: column;
        align-items: stretch;
    }

    .searchWrapper {
        width: 100%;
    }
}

/* Merchant Cell */
.merchantCell {
    display: flex;
    align-items: center;
    gap: var(--spacing-3);
}

.merchantAvatar {
    width: 40px;
    height: 40px;
    border-radius: var(--radius-full);
    background: linear-gradient(135deg, var(--color-pink) 0%, var(--color-pink-light) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: var(--font-size-sm);
    color: var(--color-primary-bg);
    flex-shrink: 0;
}

.merchantInfo {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.merchantName {
    font-weight: 500;
    color: var(--color-white);
}

.merchantEmail {
    font-size: var(--font-size-xs);
    color: var(--color-gray-500);
}

/* Table cells */
.cityText {
    color: var(--color-gray-300);
}

.categoryBadge {
    padding: var(--spacing-1) var(--spacing-2);
    background: rgba(255, 255, 255, 0.05);
    border-radius: var(--radius-md);
    font-size: var(--font-size-xs);
    color: var(--color-gray-300);
}

.statValue {
    font-weight: 500;
    color: var(--color-white);
}

.revenue {
    font-weight: 600;
    color: var(--color-accent);
}

.rating {
    display: flex;
    align-items: center;
    gap: var(--spacing-1);
}

.ratingIcon {
    color: var(--color-warning);
}

/* Actions */
.actions {
    display: flex;
    gap: var(--spacing-2);
}

.actionBtn {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: var(--radius-md);
    color: var(--color-gray-400);
    transition: all var(--transition-fast);
}

.actionBtn:hover {
    background: rgba(255, 255, 255, 0.1);
    color: var(--color-white);
}